import React from 'react';

// Static background — subtle grid only, no glows or orbs
export default function Background() {
  return (
    <div
      style={{
        position: 'fixed',
        inset: 0,
        zIndex: 0,
        pointerEvents: 'none',
        background: 'var(--bg)',
      }}
    >
      {/* Very faint ruled-line grid — editorial texture */}
      <div
        style={{
          position: 'absolute',
          inset: 0,
          backgroundImage: 'linear-gradient(var(--border) 1px, transparent 1px)',
          backgroundSize: '100% 64px',
          opacity: 0.4,
        }}
      />
    </div>
  );
}
